.twitchPage{
  margin-top: 150px;

  .border{
    border-radius:10px;
    -webkit-border-radius:10px;
    -moz-border-radius:10px;
  }

  .border_3d{
    //rgb(204 204 204 / 41%);
    background:rgba(204, 204, 204, 0.3);
    border:1px solid rgba(204, 204, 204, 0.3);

    //rgb(187 187 187 / 41%);
    border-bottom:3px solid rgba(187, 187, 187, 0.3);

    //rgb(221 221 221 / 41%);
    border-top:1px solid rgba(221, 221, 221, 0.3);
    border-radius:10px;
    -webkit-border-radius:10px;
    -moz-border-radius:10px;
  }

  .boannTwitchPlayer{
    box-shadow:0 10px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19) !important;
  }

  .TwitchPlayer{
    box-shadow:0 10px 16px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19) !important;
  }

  .online{ 
    background-color: darkgreen;
  }
  
  ul{
     list-style: none; 
     padding: unset;
    li{
      a{ 
        display: block;
        width: 100%;
        padding: 5px 10px; 
      }      
    }
  
  }

  .streamInfo{
    margin: 10px 0px;
    color: white;
    
    .placeholder{
      .tw_avatar{
        width: 70px;
        float: left;
        margin-right: 20px;                
      }
    }

    .text_info{
     display: block;
      h1{ 
          font-family: var(--font-display);
          line-height: 1.2;
          font-size: 20pt; 
          font-weight: 600;
          cursor: pointer;
      }
      
      h2{ 
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          font-size: 11pt;
          overflow: hidden;
          white-space: nowrap;
          width: 55%;
          text-overflow: ellipsis; 
          word-break: break-word;
          font-weight: 600;
      }
      
      a{
        font-size: 10pt; 
        text-decoration: underline;        
      }
    }
  }
}