// NAVBAR
.boann_navbar{
  background-color: var(--Darkcolor);
  height: 60px;

  .boann_logo{
    width: 70px;
    margin-top: -3px;
  }

  .show{
    background-color: var(--Darkcolor);
    box-shadow: 2px 2px 5px;
    .dropdown-item{
      color: white;
    }
  }

  .dropdown {
    .dropdown-primary{
        .dropdown-item:hover, .dropdown-item:active {
            background-color: rgba(0, 0, 0, 0.2)!important;
        }
    }
  }
}




