@import 'base/settings';

.boannApp {
    background-color: var(--Bodycolor);
    overflow-x: hidden;
    .page{
        margin-top: 100px;
        margin-bottom: 50px;
    }

    @import 'base/navbar';
    @import 'base/home';
    @import 'base/twitch';
    @import 'base/card';
    @import 'base/flightMap';

    .nav_menu{
        ul{
            list-style: none;
        }
    }


    .loginFrom {
        .card-body{
            color:white;
        }

        .dark_card{
            background-color: rgba(0,0,0,0.15);
        }
    }


    .dropzone {
        margin-left: 25%;
        margin-top:20px;
        width: 360px;
        height: 205px;   
        border:2px dashed #ccc;
        color:#ccc;
        line-height:197px;
        text-align: center;
    }

    .dropzone.dragover {
        border-color: #000;
        color: #000;
    }

    .dropImg {
        display:none;
    }


}
