.homePage{
    #home video{
        width: 100%;
        background-size: cover;

        @media only screen and (max-width: 1444px) {
            display: none;
        }
    }
    //1444
    #home .background-img{
        background-image: url('https://robertsspaceindustries.com/media/8uwxim0jeierqr/source/Spider-Concept-Cathcart.jpg');
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        width: 100%;
        height: 100%;
        position: absolute;

        @media only screen and (min-width: 1444px) {
            display: none;
        }
    }
}


.boann_city{
    .overlay {margin-bottom: 80px;}
    .hero-area{
        .video-container {
            /* height: 555px; */
            width: 100%;
            position: relative;

            video{
                width: 100%;
                height: 100%;
                position: absolute;
                object-fit: cover;
                z-index: 0;
            }
            .block{
                z-index: 1;
                color:white;
                position: relative;
                text-align: center;
                padding: 210px;
                background-color:rgba(0, 0, 0, 0.30);


                filter: drop-shadow(5px 5px 4px #000000);
                h1{ font-family: 'Stencil', sans-serif; font-size:55pt;}
                .video-button{font-size:60pt;}
            }
        }



        @media only screen and (max-width: 1444px) {
            display: none;
        }

    }

    #links, #sollicitaties{
        .title{
            width:100%;
            margin-bottom: 40px;

            h2, .border{
                text-align: center;
                color: white;
                margin: auto;
                width:25%;
            }
        }

        article{
            padding-bottom: 20px;
            .card{
                background-color: var(--Bodycolor);
                .card-title{color: #fff !important;}
                .btn.btn-link{
                  color: #fff !important;
                  font-size: 12pt;
                }
                p{
                    color: #737f8a !important;
                    font-family: "Quattrocento Sans", sans-serif;
                    font-size: 12pt;
                }
                    .client-thumb{
                        border-radius: 50%;
                        margin: 0 auto;
                        max-width: 115px;
                        width: 100%;
                    }
                    img{width: 100%;}

                .card-body{

                    #list-links {
                        columns: 2;
                        width: auto;
                        margin: 0;
                        padding: 0;
                        list-style: none;
                        a{
                            color: #737f8a !important;
                            font-family: "Quattrocento Sans", sans-serif;
                            font-size: 9pt;
                        }
                    }
                }
            }

            .card[class*=border]{
                  border: 1px solid var(--Darkcolor);
            }
        }
    }

    #sollicitaties{margin-top: 40px;}
}
