:root {
  --Bodycolor: #2d3651;
  --Darkcolor: #282F44;
  --Lightcolor:  #F8F3F2;
  --Accentcolor: #1F92EA;
  --font-display: "Roobert","Inter","Helvetica Neue",Helvetica,Arial,sans-serif;
}

*{transition: 0.5s;}
html { scroll-behavior: smooth; }

a:link{color: var(--Lightcolor);}
a:visited{color: var(--Lightcolor);}
a:active{color: var(--Lightcolor);}
a:hover{color: var(--Accentcolor);}


.boann_button{
    background-color: var(--Darkcolor);
    cursor: pointer;
    font-size: 1.1em;
    font-family: 'Roboto', sans-serif;
    border: none;
    //padding: 2%;

    &:hover{
      background-color: var(--Accentcolor);
      color: white;
    }
}




footer{
  background-color: var(--Darkcolor);    
  @media (max-width: 768px) {
    //margin-bottom: 250px;
  }
}
.boann_footer{
    position: absolute;
    bottom: 0;
    width: 100%;
}